import React,{PureComponent} from 'react';
import '../css/card.css';
import FileUploader from 'react-firebase-file-uploader';
import * as firebase from "firebase";
import Modal from 'react-modal';
require('firebase/storage');
require('firebase/database');
require('firebase/auth');


class Card extends PureComponent {

    state={
        modelo:'',
        nombre:'',
        url:'',
        modalIsOpen: false,
        disa: false,
        ct: ''
        }

        componentWillMount(){
            this.setState({
                modelo: this.props.post.modelo,
                nombre: this.props.post.nombre,
                url: this.props.post.url,
            })
        }

        handleUploadSuccess = filename => {
        
            firebase
              .storage()
              .ref("images")
              .child(filename)
              .getDownloadURL()
              .then(url => this.setState({ url: url, disa: false, ct: '' }));
          };

     openModal =() => {
        this.setState({modalIsOpen: !this.state.modalIsOpen});
        console.log(this.props.post);
      }

      editar(){

        var user = firebase.auth().currentUser.uid;
        firebase.database().ref(`users/${user}/posts/${this.props.post.key}`).update({
            modelo: this.props.post.modelo !== this.state.modelo ? this.state.modelo : this.props.post.modelo,
            nombre: this.props.post.nombre !== this.state.nombre ? this.state.nombre : this.props.post.nombre,
            url: this.props.post.url !== this.state.url ? this.state.url : this.props.post.url,
        })
       this.openModal() 
    }
    
    borrar(){
        var user = firebase.auth().currentUser.uid;
        firebase.database().ref(`users/${user}/posts/${this.props.post.key}`).remove();
        this.openModal() 
    }

    handleUploadStart = () => {
        this.setState({
          disa: true,
          ct:'Cargando...'
        })
    }
      
    

    render() {

        return(
        <div >
                <Modal
              isOpen={this.state.modalIsOpen}
              >
                          <div className='modalcontain' >
    
    
    <div className='modalwrap'>
                          <button className='closemoda' onClick={()=>this.openModal()} >X</button>
    
    
    <img src={require('../assets/img/car.png')} className='logo'  alt='logo' />
    <h2>Editar</h2>
    <form  >
            <input type='text' defaultValue={this.props.post.modelo}  onChange={(e)=>{this.setState({modelo:e.target.value})}} />
            <input type='text' defaultValue={this.props.post.nombre} onChange={(p)=>{this.setState({nombre:p.target.value})}} />
            <FileUploader
                accept="image/*"
                name="avatar"
                randomizeFilename
                storageRef={firebase.storage().ref('images')}
                onUploadStart={this.handleUploadStart}
                onUploadSuccess={this.handleUploadSuccess}
                />
            <label htmlFor="">{this.state.ct}</label>
        </form>
        <button onClick={()=>{this.editar()}} disabled={this.state.disa} className='edit'>Editar</button>
        <button  onClick={()=>{this.borrar()}}className='edit'>borrar</button>
    </div>
    </div>
              </Modal>
            
            <div className='CardContain' onClick={()=>{this.openModal()}}>
            <div
            className='kiki'
            style={{
                background:`url(${this.props.post.url})`,
                height:300, width:300,
                borderTopLeftRadius: 8,
                borderTopRightRadius: 8,
                backgroundRepeat:'no-repeat', 
                
            }}
            ></div>
            <div className='info' >
            <h3 className='modelo' >{this.props.post.modelo}</h3>
            <p className='Due単o' >{this.props.post.nombre}</p>
            </div>
            </div>
            </div>
        )
    }
}

export default Card;